import * as types from "../constants/ActionType";

var data = JSON.parse(localStorage.getItem("cart"));

var initinalState = data ? data : [];

var findIndex = (state, idProduct) => {
  for (let i = 0; i < state.length; i++)
    if (state[i].product.id === idProduct) return i;
  return -1;
};

export var cart = (state = initinalState, action) => {
  var index = -1;
  switch (action.type) {
    case types.SAVE_PRODUCT_TO_CART:
      console.log(action);
      index = findIndex(state, action.product.id);
      if (index === -1) {
        var cartItem = {
          product: action.product,
          quantity: action.quantity,
        };
        state.push(cartItem);
      } else {
        if (state[index].quantity === 1 && action.quantity === -1) {
        } else {
          var cloneCartItem = {
            product: action.product,
            quantity: state[index].quantity + action.quantity,
          };
          state[index] = cloneCartItem;
        }
      }
      localStorage.setItem('cart', JSON.stringify(state));
      return [...state];
    case types.DELETE_PRODUCT_TO_CART:
      index = findIndex(state, action.idProduct);
      if (index !== -1) state.splice(index, 1);
      localStorage.setItem('cart', JSON.stringify(state));
      return [...state];
    default:
      return [...state];
  }
};
