import React, { Component } from "react";
import * as message from "../constants/Message";

export default class ProductItem extends Component {
  render() {
    var { product } = this.props;
    
    return (
      <div className="col-lg-4 col-md-6 mb-r ProductItem">
        <div className="card text-center card-cascade narrower">
          <div className="view overlay hm-white-slight z-depth-1">
            <img src={product.image} className="img-fluid" alt={product.name} />
            <a>
              <div className="mask waves-light waves-effect waves-light"></div>
            </a>
          </div>
          <div className="card-body">
            <h4 className="card-title">
              <strong>
                <a>{product.name}</a>
              </strong>
            </h4>
            <ul className="rating">
              {this.showRating(product.rating)}
            </ul>
            <p className="card-text">{product.decription}</p>
            <div className="card-footer">
              <span className="left">{(product.price+"").replace(/\B(?=(\d{3})+(?!\d))/g, ',')} VND</span>
              <span className="right">
                <a
                  className="btn-floating blue-gradient"
                  data-toggle="tooltip"
                  data-placement="top"
                  title=""
                  data-original-title="Add to Cart"
                  onClick = {()=>{this.onSaveProductToCart(product, 1)}}
                >
                  <i className="fa fa-shopping-cart"></i>
                </a>
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  }

  onSaveProductToCart = (product, quantity)=> {
    this.props.saveProductToCart(product, quantity);
    this.props.changeMessage(message.MSG_ADD_TO_CART_SUCCESS);
  }
  showRating(rating) {
    var arr = [];
    for (let i = 0; i < rating; i++)
      arr.push(
        <li>
          <i className="fa fa-star"></i>
        </li>
      );

    for (let i = 5; i > rating; i--)
      arr.push(
        <li>
          <i className="fa fa-star-o"></i>
        </li>
      );
    return arr;
  }
}