import React, { Component } from "react";
import * as messages from "../constants/Message";
import { message } from "../reducers/message";

export default class CartItem extends Component {
  render() {
    var { cartItem } = this.props;
    return (
      <tr className="CartItem">
        <th scope="row">
          <img
            src={cartItem.product.image}
            alt={cartItem.product.name}
            className="img-fluid z-depth-0"
          />
        </th>
        <td>
          <h5>
            <strong>{cartItem.product.name}</strong>
          </h5>
        </td>
        <td>{(cartItem.product.price+"").replace(/\B(?=(\d{3})+(?!\d))/g, ',')} VND</td>
        <td className="center-on-small-only">
          <span className="qty">{cartItem.quantity} </span>
          <div className="btn-group radio-group" data-toggle="buttons">
            <label
              className="
                            btn btn-sm btn-primary btn-rounded
                            waves-effect waves-light
                          "
              onClick={() => {
                this.onSaveProductToCart(cartItem.product, -1);
              }}
            >
              <a>—</a>
            </label>
            <label
              className="
                            btn btn-sm btn-primary btn-rounded
                            waves-effect waves-light
                          "
              onClick={() => {
                this.onSaveProductToCart(cartItem.product, 1);
              }}
            >
              <a>+</a>
            </label>
          </div>
        </td>
        <td>{(this.totalPriceCartItem(cartItem)+"").replace(/\B(?=(\d{3})+(?!\d))/g, ',')} VND</td>
        <td>
          <button
            type="button"
            className="btn btn-sm btn-primary waves-effect waves-light"
            data-toggle="tooltip"
            data-placement="top"
            title=""
            data-original-title="Remove item"
            onClick={()=>{this.onDeleteProductToCart(cartItem.product.id);}}
          >
            X
          </button>
        </td>
      </tr>
    );
  }
  onSaveProductToCart = (product, quantity) => {
    this.props.saveProductToCart(product, quantity);
    this.props.changeMessage(messages.MSG_UPDATE_TO_CART_SUCCESS);
  };

  onDeleteProductToCart = (idProduct)=> {
    this.props.deleteProductToCart(idProduct);
    this.props.changeMessage(messages.MSG_DELETE_TO_CART_SUCCESS);
  }

  totalPriceCartItem = (cartItem) => {
    return cartItem.product.price * cartItem.quantity;
  };
}
