import React, { Component } from "react";
import { connect } from "react-redux";
import { actChangeMessage, actSaveProductToCart } from "../actions/Actions";
import ProductItem from "../component/ProductItem";
import Products from "../component/Products";
import { message } from "../reducers/message";

class ProductsContainer extends Component {
  render() {
    var { products } = this.props;
    return <Products>{this.showProductItem(products)}</Products>;
  }

  showProductItem = (products) => {
    var {saveProductToCart, changeMessage} = this.props;
    var elementProduct = products.map((element, index) => {
      return (
        <ProductItem
          product={element}
          key={index}
          saveProductToCart={saveProductToCart}
          changeMessage={changeMessage}
        />
      );
    });
    return elementProduct;
  };
}

var mapStateToProps = (state) => {
  return {
    products: state.products,
  };
};

var mapDispatchToProps = (dispatch, props) => {
  return {
    saveProductToCart: (product, quantity) => {
      dispatch(actSaveProductToCart(product, quantity));
    },
    changeMessage: (message) => {
      dispatch(actChangeMessage(message));
    },
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(ProductsContainer);
