import React, { Component } from "react";
import { connect } from "react-redux";
import {
  actChangeMessage,
  actDeleteProductToCart,
  actSaveProductToCart,
} from "../actions/Actions";
import Cart from "../component/Cart";
import CartItem from "../component/CartItem";
import TotalPrice from "../component/TotalPrice";
import { MSG_CART_EMPTY } from "../constants/Message";
import * as messages from "../constants/Message";
class CartContainer extends Component {
  

  render() {
    var { cart } = this.props;

    return (
      <Cart
        showCartItem={this.showCartItem(cart)}
        showTotalPrice={this.showTotalPrice(cart)}
      />
    );
  }
  showCartItem = (cart) => {
    var { saveProductToCart, deleteProductToCart, changeMessage } = this.props;
    if (cart.length !== 0) {
      var elementCart = cart.map((element, index) => {
        return (
          <CartItem
            key={index}
            cartItem={element}
            saveProductToCart={saveProductToCart}
            deleteProductToCart={deleteProductToCart}
            changeMessage={changeMessage}
          />
        );
      });
      return elementCart;
    }
    return MSG_CART_EMPTY;
  };

  showTotalPrice = (cart) => {
    if (cart.length !== 0) return <TotalPrice cart={cart} />;
    else return null;
  };
}

var mapStateToProps = (state) => {
  return {
    cart: state.cart,
  };
};

var mapDispatchToProps = (dispatch) => {
  return {
    saveProductToCart: (product, quantity) => {
      dispatch(actSaveProductToCart(product, quantity));
    },
    deleteProductToCart: (idProduct, quantity) => {
      dispatch(actDeleteProductToCart(idProduct));
    },
    changeMessage: (message) => {
      dispatch(actChangeMessage(message));
    },
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(CartContainer);
