import * as types from "../constants/ActionType";
export const actSaveProductToCart = (product, quantity) => {
  return {
    type: types.SAVE_PRODUCT_TO_CART,
    product,
    quantity,
  };
};

export const actDeleteProductToCart = (idProduct) => {
  return {
    type: types.DELETE_PRODUCT_TO_CART,
    idProduct,
  };
};

export const actChangeMessage = (message) => {
  return {
    type: types.CHANGE_MESSAGE,
    message,
  };
};
