import * as types from "../constants/ActionType";
import * as messages from "../constants/Message";
var initinalState = messages.MSG_WELLCOM;

export var message = (state = initinalState, action) => {
  switch (action.type) {
    case types.CHANGE_MESSAGE:
      state = action.message;
      return state;
    default:
      return state;
  }
};
