import React, { Component } from "react";

export default class TotalPrice extends Component {
  render() {
    var { cart } = this.props;
    return (
      <div className="TotalPrice">
        <td>
          <h4>
            <strong>Tổng Tiền</strong>
          </h4>
        </td>
        <td>
          <h4>
            <strong>{(this.totalPriceCart(cart)+"").replace(/\B(?=(\d{3})+(?!\d))/g, ',')} VND</strong>
          </h4>
        </td>
      </div>
    );
  }

  totalPriceCart = (cart) => {
    var res = cart.reduce((price, cartItem) => {
      return price + cartItem.product.price * cartItem.quantity;
    }, 0);
    return res;
  };
}
